import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import 'firebase/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { AngularFireDatabase, AngularFireList  } from '@angular/fire/database';
import { AngularFirestore,AngularFirestoreDocument,AngularFirestoreCollection  } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import 'rxjs/add/observable/throw';
import { finalize } from 'rxjs/operators';

/**
 * Generated class for the RegistroDetallePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-registro-detalle',
	templateUrl: 'registro-detalle.html',
})
export class RegistroDetallePage {
	registro;
	items=[];

	constructor(public navCtrl: NavController, public navParams: NavParams, private storage: AngularFireStorage, private firestore: AngularFirestore) {
		this.registro = navParams.data.registro;
		console.log("Detalle de la foto: "+JSON.stringify(this.registro));
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad RegistroDetallePage');

		this.firestore.collection("ServiceNetwork").snapshotChanges().subscribe(data =>{
			this.items=[];
			console.log("data"+data);
			data.forEach(element => {
				let item:any = element.payload.doc.data();
				item.id = element.payload.doc.id;
				this.items.push(item);
				console.log("item.id: "+item.id);
			})
		})
	}

	vistaPanoramica(item){
		this.firestore.doc(`ServiceNetwork/${item.id}`).delete();
		this.firestore.collection('ServiceNetwork').add({url:this.registro.url});
	}
}


