import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { SignaturePad } from 'angular2-signaturepad/signature-pad';
import { TaskPage } from '../task/task';
import { AngularFirestore,AngularFirestoreDocument,AngularFirestoreCollection  } from 'angularfire2/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { SqliteProvider } from './../../providers/sqlite/sqlite';
//import { ScreenOrientation } from '@ionic-native/screen-orientation';


/**
 * Generated class for the SignaturePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-signature',
  templateUrl: 'signature.html',
})

export class SignaturePage {

  @ViewChild(SignaturePad) public signaturePad:SignaturePad;

  constructor(public navCtrl: NavController, public navParams: NavParams, private firestore: AngularFirestore, public sqliteService: SqliteProvider, private storage: AngularFireStorage, public toastCtrl: ToastController, /* private screenOrientation: ScreenOrientation */) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SignaturePage');
    //this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.LANDSCAPE);
  }



  private signatureImage: string;
  private signaturePadOptions: Object = { // passed through to szimek/signature_pad constructor
    'minWidth': 1,
    'canvasWidth': window.innerWidth * 0.90,
    'canvasHeight': window.innerHeight * 0.65,
    'margin-left': window.innerWidth * 0.05,
    'margin-right': window.innerWidth * 0.05
  };

  drawComplete(){
    this.signatureImage = this.signaturePad.toDataURL();
    let ProjectId = this.sqliteService.GerProjectId();
    const filePath = `${ProjectId}/Firma del proyecto`;
    const pictures = this.storage.ref(filePath);
    var task = pictures.putString(this.signatureImage, 'data_url');

    const toast = this.toastCtrl.create({
      message: 'Firma agregada satisfactoriamente.',
      duration: 3000
    });
    toast.present();
    this.navCtrl.pop();
  
  }

  drawClear(){
    this.signaturePad.clear();
  }

  drawCancel(){
    this.signaturePad.clear();
    this.navCtrl.pop();
  }

}



