import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, App } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { SqliteProvider } from './../../providers/sqlite/sqlite';
import { TabsPage } from '../tabs/tabs';

import 'firebase/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { AngularFireDatabase, AngularFireList  } from '@angular/fire/database';
import { AngularFirestore,AngularFirestoreDocument,AngularFirestoreCollection  } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import 'rxjs/add/observable/throw';
import { finalize } from 'rxjs/operators';


/**
 * Generated class for the MenuPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-menu',
	templateUrl: 'menu.html',
})
export class MenuPage {

	items=[];

	constructor(public navCtrl: NavController, public navParams: NavParams, public sqliteService: SqliteProvider, private app:App, private storage: AngularFireStorage, private firestore: AngularFirestore) {
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad MenuPage');
		this.firestore.collection("ServiceNetwork").snapshotChanges().subscribe(data =>{
			this.items=[];
			console.log("data"+data);
			data.forEach(element => {
				let item:any = element.payload.doc.data();
				item.id = element.payload.doc.id;
				this.items.push(item);
				console.log("item.id: "+item.id);
			})
		})
	}

	vistaPanoramica(item){
		//this.firestore.collection('ServiceNetwork').doc(`${tar}`).collection('fotos').add(this.datas);
		this.firestore.doc(`ServiceNetwork/${item.id}`).delete();
		
	}

	accion(page: string) {
		console.log(page);

		switch (page) {
			case "proyectos": {
				this.navCtrl.setRoot(TabsPage);
				break;
			}
			case "salir": {
				this.sqliteService.token = null;
				this.app.getRootNav().setRoot(LoginPage);
				break;
			}
		}
	}
}


