import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { Geolocation, Geoposition } from '@ionic-native/geolocation';
import { PicturePage } from '../picture/picture';
import { TicketsPage } from '../Tickets/tickets';
import { TicketsPorTareaPage } from '../tickets-por-tarea/tickets-por-tarea';
import { TicketDetallePage } from '../ticket-detalle/ticket-detalle';
import { RegistroPage } from '../registro/registro';
import { TaskevidencePage } from '../taskevidence/taskevidence';
import { TypephotoPage } from '../typephoto/typephoto';
import { Typephoto2Page } from '../typephoto2/typephoto2';
import { NotasPage } from '../notas/notas';
import { Http, Headers } from '@angular/http';
import { NuevoregistroPage } from '../../pages/nuevoregistro/nuevoregistro';
import { Nuevoregistro2Page } from '../../pages/nuevoregistro2/nuevoregistro2';
import { SqliteProvider } from './../../providers/sqlite/sqlite';
import { NgForm } from '@angular/forms';
import { AlertController } from 'ionic-angular';
import { enlace, headers2 } from '../../api.component';
@IonicPage()

@Component({
	selector: 'page-detail',
	templateUrl: 'detail.html',
})

export class DetailPage {
	item;

	base64Image = [];
	Image = [];
	NombreTarea;
	idTarea;
	knobValues: any;
	ip2;
	token;
	rango = [];
	avanceActual;

	constructor(public navCtrl: NavController, public http: Http, public navParams: NavParams, private camera: Camera, private geolocation: Geolocation, public sqliteService: SqliteProvider, public alertCtrl: AlertController, private modal: ModalController) {
		this.item = navParams.data.task;
		this.sqliteService.tarea_id = this.item.tareaId;
		this.sqliteService.NombreTarea = this.item.nombreTarea;
		this.ip2 = this.sqliteService.ip2;
		this.token = this.sqliteService.token;

		
		console.log("Tarea id"+this.item.task_id);
		
		console.log("item "+this.item.defTarea);
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad DetailPage');
		this.NombreTarea = this.item.nombreTarea;
		 this.NombreTarea = this.sqliteService.NombreTarea;
		 this.idTarea = this.item.tareaId;
		 this.idTarea = this.sqliteService.tarea_id;

		 console.log("id Tarea "+this.item.tareaId);
		 console.log("nombre Tarea "+this.NombreTarea);

		 this.mostrarPorcentaje(event);

		 this.iniciarInstalado();

	}

	iniciarInstalado(){
		
		var url = enlace + "sumaInstalado";
		
		var data ={
			tarea: this.sqliteService.GetTareaId(),
			proyecto: this.sqliteService.GerProjectId(),
		}
		
		this.http.post(url, JSON.stringify(data), { headers: headers2 }).subscribe(Response => {
			var respuesta = Response.json();
			
			respuesta.forEach(element =>{
				element.TotalInstalado
				this.avanceActual = element.TotalInstalado;
				console.log("Avance Actual: "+this.avanceActual);
			})
					
		});
			
	}

	openModal(){
		let item = this.navParams.data.task;
		const myModal = this.modal.create('ModalPage', {item:item});

		myModal.present();
	}

	showPrompt() {
		const prompt = this.alertCtrl.create({
		  title: 'Agregar',
		  inputs: [
			{
			  label: 'descricion',
			  name: 'descripcion',
			  placeholder: 'Descripción'
			},
			{
				label: 'cantidad',
				value: this.item.cantidad,
			  },
			  {
				name: 'instalado',
				placeholder: 'Instalado'
			  },
		  ],
		  buttons: [
			{
			  text: 'Cancel',
			  handler: data => {
				console.log('Cancel clicked');
			  }
			},
			{
			  text: 'Save',
			  handler: data => {
				console.log('Saved clicked'+JSON.stringify(data));
			  }
			}
		  ]
		});
		prompt.present();
	  }

	swipeEvent(e) {
		
		if(e.direction == 4) {
		 //console.log("izquierda");
		 this.navCtrl.pop();
	   }
	   }

	   

	mostrarPorcentaje(event){
		/* this.knobValues 
		console.log(event.value); */

		let headers2 = new Headers();
    headers2.append('Accept','application/json');
    headers2.append('content-type','application/json');
	headers2.append('Authorization','Bearer '+this.token);

	let data ={id: this.idTarea}
	
		let url = this.ip2 + "/buscarPorcentaje";

		this.http.post(url, JSON.stringify(data), {headers: headers2} ).subscribe(data => {
			var knobValuess = data.json();
			this.knobValues = knobValuess.porcentaje;
			console.log("porcentaje "+this.knobValues);
		})
	}

	porcentaje(event){
		//this.knobValues 
		console.log(event)


		let headers2 = new Headers();
    headers2.append('Accept','application/json');
    headers2.append('content-type','application/json');
	headers2.append('Authorization','Bearer '+this.token);

	this.rango.push(event);
	console.log("Array porcentaje "+JSON.stringify(this.rango));

	let data ={
		id: this.idTarea,
		completed_percentage: event
	}
	
		let url = this.ip2 + "/actualizarPorcentaje";

		this.http.post(url, JSON.stringify(data), {headers: headers2} ).subscribe(data => {
			var knobValuess = data.json();
			this.knobValues = knobValuess.completed_percentage;
			console.log("porcentaje "+this.knobValues);
		}); 

	}

	/*
	OpenCamera(){
	  this.navCtrl.push(PicturePage);
	}
	 OpenNotas(){
	  this.navCtrl.push(NotasPage);
	}
	 OpenRegistro(){
	  let item = this.navParams.data.item;
	  console.log("Selected Item", item);
	  this.navCtrl.push(RegistroPage);
	}
	  OpenTickets(){
		let item = this.navParams.data.item;
		console.log("Selected Item", item);
	  this.navCtrl.push(TicketsPage, { item: item });
	}
	*/

	Open(page: string) {
		let item = this.navParams.data.task;

		console.log("Selected Item", item);
		console.log(page);
		switch (page) {
			case "notas": {
				this.navCtrl.push(NotasPage, { item: item });
				break;
			}
			case "avance": {
				this.sqliteService.tipofoto = "1";
				this.navCtrl.push(RegistroPage, { item: item });
				break;
			}
			case "evidencia": {
				this.sqliteService.tipofoto = "2";
				this.navCtrl.push(TaskevidencePage, { item: item });
				break;
			}
			case "tickets": {
				this.navCtrl.push(TicketsPorTareaPage, { item: item });
				break;
			}
			case "photo": {
				this.navCtrl.push(NuevoregistroPage, { item: item });
				break;
			}
			case "photoGallery": {
				this.navCtrl.push(Nuevoregistro2Page, { item: item });
				break;
			}
		}

		
	}

	

	ticketSelected(ticket: string) {
		//console.log("Selected Ticket", ticket);
		this.navCtrl.push(TicketDetallePage, { ticket: ticket });
	}
}


