import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import { RegistroPage } from '../registro/registro';
import { TaskevidencePage } from '../taskevidence/taskevidence';
import { SqliteProvider } from './../../providers/sqlite/sqlite';
/**
 * Generated class for the TypephotoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
	selector: 'page-typephoto',
	templateUrl: 'typephoto.html',
})
export class TypephotoPage {
	item;

	constructor(public navCtrl: NavController, public http: Http, public navParams: NavParams, public sqliteService: SqliteProvider) {
		this.item = navParams.data.item;
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad TypephotoPage');
	}

	Open(page: string) {
		let item = this.navParams.data.item;

		this.sqliteService.tipofoto = "1";

		console.log("Selected Item", item);
		console.log(page);
		switch (page) {
			case "A": {
				this.navCtrl.push(RegistroPage, { item: item });
				break;
			}
			case "E": {
				this.navCtrl.push(TaskevidencePage, { item: item });
				break;
			}
		}
	}
}



