import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http} from '@angular/http';
import { RegistroPage } from '../registro/registro';
import { TaskevidencePage } from '../taskevidence/taskevidence';
import { SqliteProvider } from './../../providers/sqlite/sqlite';

/**
 * Generated class for the Typephoto2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-typephoto2',
  templateUrl: 'typephoto2.html',
})
export class Typephoto2Page {
  item;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public sqliteService: SqliteProvider) {
    this.item = navParams.data.item;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Typephoto2Page');
  }

  Open(page: string){
    let item = this.navParams.data.item;

    this.sqliteService.tipofoto = "2";

    console.log("Selected Item", item);
    console.log(page);
    switch(page) { 
      case "A":{ 
        this.navCtrl.push(RegistroPage, { item: item });
        break; 
      } 
      case "E":{ 
        this.navCtrl.push(TaskevidencePage, { item: item });
        break; 
      }
    }
  }

}



