	ionViewDidLoad() {
		console.log('ionViewDidLoad RegistroPage');
		let taskId = this.sqliteService.GetTareaId();
		this.collection = this.firestore.collection(`fotosAvance/${taskId}/fotos`);
		this.todo = this.collection.valueChanges();
	}
